*,
*::after,
*::before {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -o-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body {
  background-color: white;
  margin: 0px;
  min-width: fit-content;
}
h1 {
  font-family: Sansation Light;
  font-style: normal;
  font-weight: 300;
  font-size: 28px;
  line-height: 31px;
  color: #FFFFFF;
}
h3 {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  text-align: center;
  color: #C2B5B5;
  margin: 0px;
}
h4 {
  font-family: Sansation Light;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 31px;
  color: #FFFFFF;
}

._mainBackground {
  background: url(../img/mainBackground.png) center bottom no-repeat;
}
._contactsBackground{
  background: url(../img/contactsBackground.png) center bottom no-repeat;
}
._mainBackground, ._contactsBackground {
  background-blend-mode: lighten;
  background-size: cover;
}
.anchor:hover {
  cursor: pointer;
  color: black;
}
