.navigation {
  height: 45px;
  top: 0px;
  margin-top: 20px;
  width: 70%;
  position: sticky;
  text-align: center;
  align-items: center;  
  z-index: 10;
}

.menu {
  margin: auto;
  list-style: none;
  width: 1000px;
  position: relative;
  overflow: hidden;
}
.menu li {
  display: inline-block;
  padding: 0 35px; 
  margin: 0px;
  height: 45px;
  right: 50%;
}
